<template>
  <div class="widget-body">
    <loading v-if="!loaded"/>
    <div class="row" v-else>
      <div class="col-xs-12 col-md-12">
        <vuestic-widget>

          <div class="row show-buttons-area">
            <router-link
              :to="{ name: 'consults' }"
              class="btn btn-info btn-micro"
            >
              {{'menu.consults.consults' | translate}}
            </router-link>
            <router-link 
              class="btn btn-primary btn-micro"
              :to="{ name: 'update-consult', params: { id: this.$route.params.id } }"
            >
              {{'buttons.update' | translate}}
            </router-link>
          </div>

          <div class="row">
            <div 
              class="col vue-misc well col-md-6"
              style="direction: rtl;" 
              if="consult.question" 
              v-html="consult.question"
            >
            </div>
            <div class="col-md-6 vue-misc">
              <vuestic-chat v-model="answers"></vuestic-chat>
            </div>
          </div>
        </vuestic-widget>
      </div>
    </div>
  </div>
</template>

<script>
  import Vue from 'vue'
  import api from 'src/packages/Api'
  import Loading from 'partials/Loading'

  Vue.component('loading', Loading)

  export default {
    name: 'consult',
    data () {
      return {
        consult: {},
        loaded: false,
        id: null,
        answers: [],
        chatMessages: [
          {
            text: 'Hello! So glad you liked my work. Do you want me to shoot you?',
            yours: false
          },
          {
            text: 'Yeah, that would be cool. Maybe this Sunday at 3 pm?',
            yours: true
          },
          {
            text: 'Sounds great! See you later!',
            yours: false
          },
          {
            text: 'Should I bring a lightbox with me?',
            yours: true
          },
          {
            text: 'No, thanks. There is no need. Can we set up a meeting earlier?',
            yours: false
          },
          {
            text: 'I\'m working on Vuestic, so let\'s meet at 3pm. Thanks!',
            yours: true
          }
        ]
      }
    },
    created () {
      api.get(`consults/${this.$route.params.id}`).then(res => {
        let id = null

        this.consult = res.data
        this.answers = res.data.answers.data.map(a => {
          if (!id) id = a.writer_id

          return {
            text: a.content, 
            yours: id === a.writer_id ? true : false
          }
        })

        this.loaded = true
      }).catch(error => {
        this.$store.dispatch('addToastMessage',
          { text: Vue.i18n.translate(`errors.${error.data.status_code}`) }
        )
        this.$router.push({ name: 'consults' })
      })
    }
  }
</script>

<style lang="scss">
.chat-controls {
  display: none;
}  
</style>
